"use client"

import { useState } from "react"
import Link from "next/link"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"

// Mock patient data - in a real app, this would come from an API
const mockPatients = [
  {
    diaryNumber: "F/101",
    name: "John Doe",
    fatherName: "James Doe",
    phone: "1234567890",
    lastVisit: "2024-01-15",
    totalVisits: 3,
  },
  {
    diaryNumber: "CH123",
    name: "Jane Smith",
    fatherName: "John Smith",
    phone: "9876543210",
    lastVisit: "2024-01-10",
    totalVisits: 5,
  },
]

export default function PatientSearch() {
  const [searchTerm, setSearchTerm] = useState("")
  const [filteredPatients, setFilteredPatients] = useState(mockPatients)

  const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    const term = e.target.value.toLowerCase()
    setSearchTerm(term)

    const filtered = mockPatients.filter(
      (patient) =>
        patient.name.toLowerCase().includes(term) ||
        patient.fatherName.toLowerCase().includes(term) ||
        patient.phone.includes(term) ||
        patient.diaryNumber.toLowerCase().includes(term)
    )
    setFilteredPatients(filtered)
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <Card className="p-6 bg-white">
        <div className="mb-6">
          <h1 className="text-2xl font-bold text-gray-900 mb-4">Patient Search</h1>
          <Input
            type="text"
            placeholder="Search by Name, Father's Name, Phone, or Diary Number"
            value={searchTerm}
            onChange={handleSearch}
            className="max-w-xl"
          />
        </div>

        <div className="rounded-md border">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Diary Number</TableHead>
                <TableHead>Name</TableHead>
                <TableHead>Phone</TableHead>
                <TableHead>Last Visit</TableHead>
                <TableHead>Total Visits</TableHead>
                <TableHead>Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {filteredPatients.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={6} className="text-center text-gray-500">
                    No patients found
                  </TableCell>
                </TableRow>
              ) : (
                filteredPatients.map((patient) => (
                  <TableRow key={patient.diaryNumber}>
                    <TableCell>{patient.diaryNumber}</TableCell>
                    <TableCell>
                      <div>
                        <p className="font-medium">{patient.name}</p>
                        <p className="text-sm text-gray-500">
                          s/o {patient.fatherName}
                        </p>
                      </div>
                    </TableCell>
                    <TableCell>{patient.phone}</TableCell>
                    <TableCell>{patient.lastVisit}</TableCell>
                    <TableCell>{patient.totalVisits}</TableCell>
                    <TableCell>
                      <div className="flex space-x-2">
                        <Link href={`/patient/${patient.diaryNumber}/visit-entry`}>
                          <Button variant="outline" size="sm">
                            Add Visit
                          </Button>
                        </Link>
                        <Link href={`/patient/${patient.diaryNumber}`}>
                          <Button variant="outline" size="sm">
                            View Record
                          </Button>
                        </Link>
                        <Link href={`/patient/${patient.diaryNumber}/edit`}>
                          <Button variant="outline" size="sm">
                            Edit Info
                          </Button>
                        </Link>
                      </div>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </div>
      </Card>
    </div>
  )
}
